a {
  text-decoration: none;
}

h1 {
  font-family: Staatliches;
  font-size: 120px;
} 

#category {
  font-family: Staatliches;
  font-weight: 500;
}

#title {
  letter-spacing: 0.4px;
  font-size: 22px;
  font-size: 1.375rem;
  line-height: 1.13636;
}

#banner {
  margin: 20px;
  height: 800px;
}

#editor {
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.75;
}

.uk-navbar-container {
  font-family: Staatliches;
}

.login {
  height: 100vh
}

img:hover {
  opacity: 1;
  transition: opacity 0.25s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.textbox {
  height:30px;
  font-size:12pt;
}

.recipe-card{
-webkit-box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
-moz-box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
box-shadow:0 0 10px rgba(0, 0, 0, 0.5);
}

html, body{
  margin: 0;
  height: 100%;
}